import { Component, OnInit } from 'angular2/core';

import { WorkDetailService } from './work-detail.service';
import { Logger } from '../blocks/logger.service.ts';
import { Work } from './work';

@Component({
    selector: 'work',
    styles: [`
        h2 {
            margin-left: 20px;
            font-size: 24px;
            font-weight: 400;
        }
    `],
    template: require('./work-detail.component.html'),
    directives: [],
    providers: [WorkDetailService, Logger]
})
export class WorkComponent implements OnInit {
    constructor(
        private _service: WorkDetailService,
        private _logger: Logger) { }
    experience: Work[];

    ngOnInit() {
        this._service.getWork()
            .then(experience => this.experience = experience)
            .catch(error => this._logger.error(error));
    }

}



