import { Component, OnInit } from 'angular2/core';

import { TechDetailService } from './tech-detail.service';
import { Logger } from '../blocks/logger.service.ts';
import { Tech } from './tech';

@Component({
    selector: 'tech-list',
    styles: [`
        h2 {
            margin-left: 20px;
            font-size: 24px;
            font-weight: 400;
        }
    `],
    template: require('./tech-list.component.html'),
    directives: [],
    providers: [TechDetailService, Logger]
})
export class TechListComponent implements OnInit {
    constructor(private _service: TechDetailService, private _logger: Logger) { }
    technologies: Tech[];
    ngOnInit() {
        this._service.getTech()
            .then(technologies => this.technologies = technologies)
            .catch(error => this._logger.error(error));
    }
}



