import { Component, OnInit } from 'angular2/core';

import { EduService } from './edu.service';
import { Edu } from './edu';
import { Logger } from '../blocks/logger.service';

@Component({
    selector: 'edu',
    styles: [require('./edu.component.scss')],
    template: require('./edu.component.html'),
    directives: [],
    providers: [EduService, Logger]
})
export class EduComponent implements OnInit {
    constructor(private _service: EduService, private _logger: Logger) { }
    education: Edu[];
    ngOnInit() {
        this._service.getEdu()
            .then(education => this.education = education)
            .catch(error => this._logger.error(error));
     }
}



