import {Component, OnInit, enableProdMode, provide} from 'angular2/core';
import {ELEMENT_PROBE_PROVIDERS} from 'angular2/platform/browser';
import {ROUTER_PROVIDERS, HashLocationStrategy, LocationStrategy, RouteConfig, ROUTER_DIRECTIVES } from 'angular2/router';
import {FORM_PROVIDERS} from 'angular2/common';
import {HTTP_PROVIDERS} from 'angular2/http';

import {Logger} from './blocks/logger.service';
import {AppService} from './app.service';
import {HomeComponent} from './home/home.component';
import {AboutComponent} from './about/about.component';
import {WorkComponent} from './work/work-detail.component';
import {EduComponent} from './edu/edu.component';

const ENV_PROVIDERS = [];
// depending on the env mode, enable prod mode or add debugging modules
if (process.env.ENV === 'prod') {
  enableProdMode();
} else {
  ENV_PROVIDERS.push(ELEMENT_PROBE_PROVIDERS);
}

@Component({
  selector: 'app', // <app></app>
  providers: [
    ...FORM_PROVIDERS,
    ...HTTP_PROVIDERS,
    ...ROUTER_PROVIDERS,
    ...ENV_PROVIDERS,
    AppService,
    provide(LocationStrategy, {useClass: HashLocationStrategy}),
    Logger
    // use #/ routes, remove this for HTML5 mode
  ],
  pipes: [],
  styles: [require('./app.component.scss')],
  template: require('./app.component.html'),
  directives: [...ROUTER_DIRECTIVES]
})
@RouteConfig([
  {path: '/', component: HomeComponent, name: 'Home', useAsDefault: true},
  {path: '/About', component: AboutComponent, name: 'About'},
  {path: '/Work', component: WorkComponent, name: 'Work'},
  {path: '/Education', component: EduComponent, name: 'Education'}
])

export class AppComponent implements OnInit {
  urlTwitter: string = 'http://twitter.com/CarolinaBlueCru';
  urlNg: string = 'http://angular.io';
  theWellImage: string = '../assets/img/thewell.png';
  myImage: string = '../assets/img/me.jpg';

  constructor(public appService: AppService, public loggerService: Logger) { }
  ngOnInit() {
    let title = this.appService.title;
    this.loggerService.log(title + ' booted!');
  }
}



