import { Injectable } from 'angular2/core';

import { Logger } from '../blocks/logger.service';
import { Tech } from './tech';

@Injectable()
export class TechDetailService {
    constructor(private _logger: Logger) { }

    private _technologies: Tech[] = [
        {
            id: 1,
            name: 'Angular2',
            desc: 'Angular2 is a javascript framework for building modular web applications developed by Google.',
            url: 'https://angular.io',
            src: 'https://christianliebel.com/wp-content/uploads/2016/02/Angular2-825x510.png'
        },
        {
            id: 2,
            name: 'TypeScript',
            desc: `TypeScript is a language for application-scale JavaScript that adds optional types, 
                   classes, and modules to JavaScript. `,
            url: 'http://www.typescriptlang.org',
            src: 'https://s.gravatar.com/avatar/17e414f1d3c2a1c190a1fe04d9850286?size=496&default=retro'
        },
        {
            id: 3,
            name: 'WebPack',
            desc: `Packs CommonJs/AMD modules for the browser. Allows splitting a codebase into multiple
                   bundles, to be loaded on demand.`,
            url: 'https://webpack.github.io',
            src: 'https://webpack.github.io/assets/logo.png'
        }
    ];

    getTech() {
        this._logger.log('Retrieved ' + this._technologies.length + ' technologies.');
        return Promise.resolve(this._technologies);
    }
}



